import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class NavbarContent extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'navbar-content',
			'clearfix',
		className
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

NavbarContent.propTypes = {
	className: PropTypes.string,
	children: PropTypes.node,
};

NavbarContent.defaultProps = {
};

export default NavbarContent;